// Vars.
$breakpoints: null;

/// Breakpoints.
/// @var {list}
$breakpoints: () !global;

/// Vendor prefixes.
/// @var {list}
$vendor-prefixes: (
    '-moz-',
    '-webkit-',
    '-ms-',
    ''
);

/// Properties that should be vendorized.
/// @var {list}
$vendor-properties: (
    'align-content',
    'align-items',
    'align-self',
    'animation',
    'animation-delay',
    'animation-direction',
    'animation-duration',
    'animation-fill-mode',
