/* Banner */

#banner {
  @include padding(10em, 0, (0, 0, _size(wrapper-edges, large) * -0.5, 0));

  z-index: -20;

  .inner {
    margin: 0 auto;
    width: _size(inner);
    color: _palette(fg-header);
    a {
      color: _palette(fg-header);
    }
  }

  .logo {
    @include vendor('transition', (
            'opacity 2s ease',
            'transform 1s ease',
    ));
    @include vendor('transform', 'translateY(0)');
    opacity: 1;
    margin: 0 0 (_size(element-margin) * 0.65) 0;

    .icon {
      border-radius: 100%;
      border: solid 2px _palette(border);
      cursor: default;
      display: inline-block;
      font-size: 2em;
      height: 2.25em;
      line-height: 2.25em;
      text-align: center;
      width: 2.25em;
    }
  }

  h2 {
    @include vendor('transition', (
            'opacity 0.5s ease',
            'transform 0.5s ease',
            'filter 0.25s ease',
    ));
    @include vendor('transform', 'translateX(0)');
    @include vendor('transition-delay', '0.05s');
    @include vendor('filter', 'blur(0)');
    opacity: 1;
    border-bottom: solid 2px _palette(border);
    font-size: 2.25em;
    margin-bottom: _size(element-margin) * 0.4;
    padding-bottom: _size(element-margin) * 0.2;
    color: _palette(fg-header);
  }

  p {
    @include vendor('transition', (
            'opacity 0.5s ease',
            'transform 0.5s ease',
            'filter 0.25s ease',
    ));
    @include vendor('transform', 'translateX(0)');
    @include vendor('transition-delay', '0.15s');
    @include vendor('filter', 'blur(0)');
    opacity: 1;
    font-family: _font(family-heading);
    font-size: 1em;
    font-weight: _font(weight-heading);
    letter-spacing: _font(kern-heading);
    line-height: 2;
    text-transform: uppercase;
    color: _palette(fg-header-light);
  }

  @include breakpoint(large) {
    @include padding(7em, 0, (0, 0, _size(wrapper-edges, large) * 0.5, 0));

    margin-bottom: (_size(wrapper-edges, large) * -1);
  }

  @include breakpoint(medium) {
    @include padding(7em, 3em, (0, 0, _size(wrapper-edges, medium) * 0.5, 0));

    margin-bottom: (_size(wrapper-edges, medium) * -1);

    .inner {
      width: 100%;
    }
  }

  @include breakpoint(small) {
    @include padding(5em, 2em, (0, 0, _size(wrapper-edges, small) * 0.5, 0));

    margin-bottom: (_size(wrapper-edges, small) * -1);

    .logo {
      margin: 0 0 (_size(element-margin) * 0.5) 0;

      .icon {
        font-size: 1.5em;
      }
    }

    h2 {
      font-size: 1.5em;
    }

    p {
      font-size: 0.8em;
    }
  }

  body.is-loading & {
    .logo {
      @include vendor('transform', 'translateY(0.5em)');
      opacity: 0;
    }

    h2 {
      opacity: 0;
      @include vendor('transform', 'translateX(0.25em)');
      @include vendor('filter', 'blur(2px)');
    }

    p {
      opacity: 0;
      @include vendor('transform', 'translateX(0.5em)');
      @include vendor('filter', 'blur(2px)');
    }
  }
}

.banner-team-member-info {
    @include vendor('transition', (
            'opacity 0.5s ease',
            'transform 0.5s ease',
            'filter 0.25s ease',
    ));
    @include vendor('transform', 'translateX(0)');
    @include vendor('transition-delay', '0.15s');
    @include vendor('filter', 'blur(0)');
    
    display: flex;
    opacity: 1;
    font-family: _font(family-heading);
    font-size: 1em;
    font-weight: _font(weight-heading);
    letter-spacing: _font(kern-heading);
    line-height: 2;
    text-transform: uppercase;
    
    body.is-loading & {
        opacity: 0;
        @include vendor('transform', 'translateX(0.5em)');
        @include vendor('filter', 'blur(2px)');
    }
    
    .team-member-image-holder {
        position: relative;
        margin-right: 1em;
      
        .alumnus-badge {
            position: absolute;
            width: 90px;
            left: 50%;
            margin-left: -45px;
            top: -12px;
            background: _palette(bg);
            text-align: center;
            border-radius: 30px 30px 30px 30px;
            color: white;
            padding: 5px 10px;
            font-size: 12px;
        }
    }
}
